.homeContainer {
  overflow: auto;
  overflow-x: hidden;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.homeHeader {
  background-image: url("../../../public/homeHeader.jpg");
  background-size: contain;
  height: 100vh;
  width: 100vw;
}
.sectionContainer {
  height: 100%;
  width: 100%;
}

.cardsContainer {
  display: flex;
  justify-content: space-between;
}
.cardsContainer {
  height: 800px;
}

.cardsContainerLarge {
  height: 1000px;
}

.infoCard {
  width: 50%;
}

.infoCardColumn {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cardImage {
  display: flex;
  align-items: end;
  background-size: cover;
  height: 100%;
  border-radius: 1.25rem;
}

.cardContentContainer {
  padding: 3rem 4rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.vectorImg {
  width: 50rem;
}

.circleDivider {
  border-radius: 50%;
  height: 8px;
  background-color: #69722f;
  width: 8px;
  margin: 10px;
}

.catalogContainer {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  margin: 0px 50px;
}

.catalogHeaderContainer {
  display: flex;
  margin: 7rem 50px 0.5rem 50px;
  justify-content: space-between;
  align-items: center;
}

.catalogHeaderTitle {
  font-size: 2rem;
  font-weight: lighter;
  line-height: 0.5rem;
}

.catalogHeaderTitle_W {
  font-weight: 650;
  margin: 3rem 0px;
  display: flex;
  justify-content: center;
}

.catalogTitleEmoji {
  width: 1.75rem;
  height: 1.75rem;
}

.accordinSection {
  display: flex;
  flex-direction: column;

  background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)),
    url("../../../public/faq_bg.jpg");
  background-size: cover;
  /*background-color: #51977a;*/
  margin: 7rem 0px;
  padding: 0px 0px 3rem 0px;
}

.accordinTabsContainer {
  display: flex;
  justify-content: center;
}

.accordinContainer {
  min-width: 45%;
}
