.shopContainer {
  text-align: center;
  overflow: hidden;
  overflow-x: hidden;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 10px;
  margin: 7rem 2rem;
  min-height: 100vh;
}
.filterPanel {
  grid-column-start: 1;
  grid-column-end: 2;
}
.productsPanel {
  grid-column-start: 2;
  grid-column-end: 10;
}
.catalogShopContainer {
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
  margin: 3rem 1.5rem;
}

.categoryTitleContainer {
  display: flex;
  margin: 0px 2rem;
  flex-direction: column;
}

.categoryTitle {
  width: 100%;
  display: flex;
  font-size: 1.5rem;
  font-weight: lighter;
  line-height: 0.2rem;
}

.separator {
  width: 100%;
  height: 2px;
  background-color: #828282;
}

.shopProductCardContainer {
  border-radius: 8px;
  overflow: hidden;
  width: 600px;
  max-width: 24rem;
  transition: transform 0.2s ease 0s;
  cursor: pointer;
  position: relative;
}

.shopCardImage {
  border-radius: 1.25rem;
  width: 100%;
  object-fit: cover;
  z-index: -1;
  position: relative;
  display: block;
}

.shopCardInfoContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  line-height: 0px;
  width: 100%;

}

.shopCardTitle {
  font-family: "Roboto", sans-serif;
  font-size: 1.2em;
  margin-block-end: 0.3em;
  color: #2f2f2f;
}

.shopCardPrice {
  color: #434343;
}

.shopCartContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btnCart {
  width: 40px;
  height: 40px;
}

.filterPanelContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.filterPanelPropsText {
  font-size: 14px;
  color: #828282;
  font-weight: 700;
}

.imageProduct {
  border-radius: 1.25rem;
  background: -moz-linear-gradient(
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.1) 100%
  );
  background: -webkit-linear-gradient(
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.1) 100%
  ); /* Chrome10+,Safari5.1+ */
}

.emojiStyling{
  right:1rem;
  position: absolute;
  top:1rem;
  max-width: 4rem;
}

.shopCardInfo{
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.productPage{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}

.productPageContainer{
  width: 100%;
  margin:5rem;
  display: flex;
  flex-direction: row;
}

.productPageMainImg{
  max-width: 100vh;
  max-height: 100vh;
  border-radius: 0.25rem;
}

.productPageContentContainer{
  width: 100%;
  padding: 0rem 5rem;
}

.productPageTitle{
  font-size: 2rem;
  font-weight: 300;
  line-height: 2rem;
}

.thumbs-wrapper > ul{
  position:absolute;
  overflow-y: auto;
}

.thumbs-wrapper > ul > li{
  height: 150px;
  position: relative;
  cursor: pointer;
  pointer-events: all !important;
}
