.catalogHeaderTitle {
  font-size: 2rem;
  font-weight: 300;
  line-height: 0.5rem;
  display: flex;
  position: relative;
}
.infoPageContainer {
  text-align: center;
  overflow: auto;
  overflow-x: hidden;
  width: 100%;
  padding: 4rem 0px;
}

.tabStyling {
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
  border-radius: 1.25rem;
  background-size: cover;
}

.textStyling {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: center;
  padding: 0px 10%;
  text-align: left;
  flex-direction: column;
  position: absolute;
  top: 0rem;
}
.textAboutUs {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  padding: 6% 1%;
  /*font-size: 1.3vw;*/
  text-align: center;
  margin: 0px 1rem;
}
.aboutUsTitle {
  height: 2.5rem;
  position: absolute;
  display: flex;
  align-items: center;
  padding: 10px;
  /*top: -1.5rem;*/
  border-radius: 1.25rem;
}
