.blogProductCardContainer {
  border-radius: 8px;
  overflow: hidden;
  width: 600px;
  transition: transform 0.2s ease 0s;
  cursor: pointer;
}

.blogCardImage {
  border-radius: 1.25rem;
  width: 100%;
  object-fit: cover;
}
.blogCardInfoContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  line-height: 0px;
}
.blogCardTitle {
  font-family: "Roboto", sans-serif;
  font-size: 1.3em;
  margin-block-end: 0.3em;
  color: #2f2f2f;
  line-height: 1.3;
}

.blogCartContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}